.container {
    margin-right: auto;
    margin-left: auto;
    padding-right: 5vw;
    padding-left: 5vw;
    max-width: calc(1130px + 10vw);
    width: 100%;
}

.container.wide {
    max-width: calc(1034px + 10vw);
}

.container.medium {
    max-width: calc(640px + 10vw);
}

.container.small {
    max-width: calc(560px + 10vw);
}
